import './App.css';
import { RouterProvider, createBrowserRouter,Navigate } from 'react-router-dom';
import Home from './components/Home';
import Root from './components/Root';
import Login from './components/Login';
import Register from './components/Register';
import Userprofile from './components/Userprofile';
import Authorprofile from './components/Authorprofile';
// import Addarticle from './components/Addarticle';
import ArticlesByAuthor from './components/ArticlesByAuthor';
import Article from './components/Article';
import Articles from './components/Articles';
import Errorpage from './components/Errorpage';
import { Suspense, lazy } from 'react';
const Addarticle=lazy(()=>import('./components/Addarticle'))
function App() {
  let router=createBrowserRouter([
    {
      path:'',
      element:<Root/>,
      errorElement:<Errorpage/>,
      children:[
        {
          path:'',
          element:<Home/>
        },
        {
          path:"/signin",
          element:<Login/>,
        },
        {
          path:"/signup",
          element:<Register/>
        },
        {
          path:"/userprofile",
          element:<Userprofile/>,
          children:[
            {
            path:"articles",
            element:<Articles/>
            },
            {
              path:"article/:articleId",
              element:<Article/>
            },
            {
              path:'',
              element:<Navigate to='articles'></Navigate>
            }
          ]
        },
        {
          path:"/authorprofile",
          element:<Authorprofile/>,
          children:[
            {
              path:"addarticle",
              element:<Suspense fallback="loading..."><Addarticle/></Suspense>
            },
            {
              path:"articles-by-author/:author",
              element:<ArticlesByAuthor/>
            },
            {
              path:"article/:articleId",
              element:<Article/>
            },
            {
              path:'',
              element:<Navigate to='articles-by-author/:author'></Navigate>
            }
          ]
        }
        
      ]
    }
  ])
  return (
   <div>
      <RouterProvider router={router}/>
   </div>
  );
}

export default App;

